@import '../styles/styles';
@import '../styles/colors';

.nav {
  @include flex();
  align-items: stretch;
  height: $height;

  background-color: darken($olive, 25%);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.item {
  @include flex();
  flex: 1;
  cursor: pointer;
  color: fade-out($white, 0.5);
}

.active {
  color: $white;
}

.content {
  height: 100%;
  padding: $gutter;
  padding-top: 0;
}

.close {
  margin: ($gutter * 2) auto $gutter;
  padding: $gutter;
  background-color: fade-out($white, 0.96);
  color: fade-out($white, 0.5);
  font-weight: bold;
  text-transform: uppercase;
  width: 200px;
}
